.cartItem {
    height: 98px;
    background-color: white;
    margin-top: 1px;
    display: flex;
    align-items: center;
    padding: 1px 5px;
    
      .product-cart {
        width: 20%;
      max-width: 50px;
      min-height: 50px;
      display: block;
      object-fit: cover;
      }
      .dataContainer {
        display: flex;
        width: 100%;
        height: 100%;
        padding: 0px 0px 3px;
        margin-top: 5px;
        align-items: baseline;
        
    
        .left {
          padding-left: 25px;
          display: flex;
          flex-direction: column;
          justify-content: space-between;
          text-overflow: ellipsis;
          width: 100%;
    
          p {
              font-weight: bold;
              font-size: 10px;
          }
    
          .buttons {
               display: flex;
               gap: 5px;
    
               button {
                   border: none;
                   cursor: pointer;
                   font-size: 12px;
                   padding: 3px 5px;
                   border-radius: 5px;
                   font-weight: bold;
                   color: #222222;
               }
          }
        }
    
        .right {
            display: flex;
            flex-direction: column;
            justify-content: space-between;
            align-items: flex-end;
    
            div {
                background-color: #FB3939;
                width: 40px;
                height: 17px;
                border-radius: 4px;
                display: flex;
                align-items: center;
                justify-content: center;
                font-size: 12px;
                font-weight: bold;
                margin: 26px 3px;
            }
            
            p {
                font-weight: bold;
            }
        }
      }
    }
    .quantity-container {
      display: flex;
      padding: 6px 2px;
      justify-content: center;
  
    }
    .quantity-title{
      display: flex;
      text-align: center;
      margin: -3px -2px -24px;
    }
    .quantity-input {
      width: 18px;
      text-align: center;
    }
    .item-title {
      height: 15px;
      text-align: left;
      color: #222222;
    }
    